.App {
  text-align: center;
}

.imgIcon {
  transition: 1s;
}
.imgIcon:hover {
  transform: scale(1.2);
}

.enable {
  opacity: 1;
  transition: 1s;
}

.not-enable {
  opacity: 0;
  transition: 1s;
}

.enableCircle {
  transform: scale(1.2);
  transition: 1s;
}
.not-enableCircle {
  transform: scale(1);
  transition: 1s;
}

.option-inner-card {
  background-color: #bf5830;
  background-size: cover;
  padding: 2rem;
  border-radius: 5px;
  max-width: 10rem;
  min-width: 5rem;
  height: 4rem;
  margin: 0rem 0.2rem;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  color: white;
}

@media only screen and (max-width: 768px) {
  .option-inner-card {
    max-width: 10rem;
    min-width: 1rem;
    margin: 0rem 0rem;
    height: 1rem;
  }
  .title-inner-card {
    font-size: 12px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
  }
  .App {
    width: auto !important;
    height: 100vw !important;
  }
  .map-image {
    width: 100vh !important;
  }
}
